<template>
  <div class="personnelManagement-container">
    <div class="title">
      <h4>人员管理</h4>
    </div>
    <personTable :table="tableData"/>
  </div>
</template>

<script>
  import personTable from './personTable'
  export default {
    data() {
      return {
        tableData:[]
      }
    },
    components: {
      personTable,
    },
    created() {
      this.findAdmiss()
    },
    methods: {
      findAdmiss() {
        let params = { pageSize: 8, offset:1, processName:'接单' }
        params = this.$qs.stringify(params)
        this.$http({
          url: 'omm/restActivitiController/findTodoTasks',
          data: params
        }).then( res => {
          if( res.status == 200 ) {
          this.tableData = res.data.result
        }
      } )
      }
    }
  }
</script>

<style lang="less" scoped>
  .title {
    font-size: 22px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ddd;
    h4 {
      font-weight: normal;
    }
  }
</style>




